.carousel{
    position: "absolute";
    margin-left: "6rem";
    opacity: ".8";
    margin-top: 2rem;
    display: flex;
    justify-content: center;
    min-height: 39rem;
    max-height: 39rem;
    border-radius: 5rem;

}

.card{
    min-height: 37rem;
    max-height: 37rem;
    min-width: 94%;
    max-width: 94%;
    border-radius: 5rem;
    position: relative;
    align-items: center;
    display: inline-block;
    justify-content: center;
    width: 96%;
}

.card img{
    height: 32rem;
    width: 100%;
}
