.card:hover {
  animation: hoverCard 0.3s forwards;
}

@keyframes hoverCard {
  0% {
    scale: 1;
    opacity: .85;
  }

  25% {
    scale: 1.025;
    opacity: .90;
  }

  50% {
    scale: 1.05;
  }

  75% {
    scale: 1.075;
    opacity: .95;
  }

  100% {
    scale: 1.1;
    opacity: 1;
  }
}
