

@media only screen and (min-width: 768px) {
.appointment {
  position: relative;
  height: 30rem;
  background-color: gray;
  border-radius: 2rem;
  width: 98%;
  margin-left: 1%;
}
.appointment img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.5;
  position: relative;
  overflow: hidden;
  z-index: 0;
  border-radius: 2rem;
}
}

@media only screen and (max-width: 768px) {
.appointment {
  position: relative;
  height: 12rem;
  background-color: gray;
    border-radius: 1rem;
  width: 98%;
  margin-left: 1%;
}
.appointment img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.5;
  position: relative;
  overflow: hidden;
  z-index: 0;
  border-radius: 1rem;

}
}